import {Options as Api} from '../../api/api.js';
import {NotificationsAC} from '../NotificationsReducer.js';
import {required} from '../Validates.js'

let initialState = {
  InputForm: {
    initialValues: {},
    FormMainItems: [
      {
        ItemName: "IdProduct",
        Explanations: "",
        Type: "Select",
        Options: [],
        Validate: [required]
      },
      {
        ItemName: "Name",
        Explanations: "",
        Type: "Text",
        Validate: [required]
      },
      {
        ItemName: "Weight",
        Explanations: "",
        Type: "Number",
        Validate: [required]
      },
      {
        ItemName: "Size",
        Explanations: "",
        Type: "Number",
        Validate: [required]
      },
      {
        ItemName: "Price",
        Explanations: "",
        Type: "Number",
        Validate: [required]
      },
      {
        ItemName: "Additive",
        Explanations: " ",
        Type: "Number",
        Validate: [required]
      },
      {
        ItemName: "Additives",
        Explanations: " ",
        Type: "SelectMultiple",
        Options: [],
        Validate: [required]
      }
    ]
  },
  Table:{
    Title: [
        {Name: "", ColumnName: "Product", Visibility: true},
        {Name: "", ColumnName: "Name", Visibility: true},
        {Name: "", ColumnName: "Weight", Visibility: true},
        {Name: "", ColumnName: "Size", Visibility: true},
        {Name: "", ColumnName: "Price", Visibility: true},
        {Name: " ", ColumnName: "Additive", Visibility: true},
        {Name: "", ColumnName: "AdditivesTable", Visibility: true}
    ],
    Content: [
    ],
    Config: {
        Add: true,
        Details: false,
        Change: true,
        Delete: true
    }
  }
};

const Reducer = (state = initialState, action) => {
	
	let stateCopy = {...state};
	
	switch (action.type) {
    case "SET_DATA_O":{
      stateCopy.InputForm.FormMainItems[0].Options = action.data.Products.map(el => {return {Key: el.Id, Data: el.Name}})
			stateCopy.InputForm.FormMainItems[6].Options = action.data.Additives.map(el => {return {Key: el.Id, Data: el.Name}})
			
      stateCopy.Table.Content = action.data.ProductOption.map(el => {
        return {
          ...el, 
          Additives: action.data.ListAdditives.filter(elf => elf.IdProductOption == el.Id).map(elm => elm.IdAdditive), 
          AdditivesTable: action.data.ListAdditives.filter(elf => elf.IdProductOption == el.Id).map(elm => elm.AdditiveName).join("; ")
        }
      });

      return stateCopy;
		}
    case "SET_INITIAL_VALUES_O":{

			stateCopy.InputForm.initialValues = {...action.data};
      return stateCopy;
		}
		default:{
			return state;
		}
	}
}

export const actionCreator = {
  setData: (data) => {
    return {type: "SET_DATA_O", data: data};
  },
  setInitialValues: (data) => {
      return {type: "SET_INITIAL_VALUES_O", data: data};
  }
}

export const ThunkCreator = {
  Get: () => {
    return (dispatch) => {
      Api.Get().then((data) => {
        dispatch(actionCreator.setData(data.Data));
      });
    }
  },
  Add: (Data) => {
    return (dispatch) => {
      Api.Insert(Data).then((response) => {
        if(!response.Error){
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  },
  Change: (Data) => {
    return (dispatch) => {
      Api.Change(Data).then((response) => {
        if(!response.Error){
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  },
  Del: (Data) => {
    return (dispatch) => {
      Api.Delete(Data).then((response) => {
        if(!response.Error){
          Api.Get().then((data) => {
            dispatch(actionCreator.setData(data.Data));
          });
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  }
}

export default Reducer

