import {Products as Api} from '../../api/api.js';
import {NotificationsAC} from '../NotificationsReducer.js';
import {required} from '../Validates.js'

let initialState = {
  InputForm: {
    initialValues: {},
    FormMainItems: [
      {
        ItemName: "Name",
        Explanations: "",
        Type: "Text",
        Validate: [required]
      },
      {
        ItemName: "Description",
        Explanations: "",
        Type: "TextArea",
        Validate: [required]
      },
      {
        ItemName: "Image",
        Explanations: "",
        Type: "File",
        Validate: [required]
      },
      {
        ItemName: "IdCategory",
        Explanations: "",
        Type: "Select",
        Options: [],
        Validate: [required]
      },
      {
        ItemName: "Branches",
        Explanations: "",
        Type: "SelectMultiple",
        Options: [],
        Validate: [required]
      }
    ]
  },
  Table:{
    Title: [
        {Name: "", ColumnName: "Name", Visibility: true},
        {Name: "", ColumnName: "Description", Visibility: true},
        {Name: "", ColumnName: "CategoryName", Visibility: true},
        {Name: "", ColumnName: "Image", Visibility: true},
        {Name: "", ColumnName: "BranchesTable", Visibility: true}
    ],
    Content: [
    ],
    Config: {
        Add: true,
        Details: false,
        Change: true,
        Delete: true
    }
  }
};

const Reducer = (state = initialState, action) => {
	
	let stateCopy = {...state};
	
	switch (action.type) {
    case "SET_DATA_P":{
      stateCopy.InputForm.FormMainItems[3].Options = action.data.Category.map(el => {return {Key: el.Id, Data: el.Name}})
      stateCopy.InputForm.FormMainItems[4].Options = action.data.Branches.map(el => {return {Key: el.Id, Data: el.Address}})
			
      stateCopy.Table.Content = action.data.Products.map(el => {
        return {
          ...el, 
          Branches: action.data.BranchProducts.filter(elf => elf.IdProduct == el.Id).map(elm => elm.IdBranch), 
          BranchesTable: action.data.BranchProducts.filter(elf => elf.IdProduct == el.Id).map(elm => elm.Address).join("; ")
        }
      });

      return stateCopy;
		}
    case "SET_INITIAL_VALUES_P":{

			stateCopy.InputForm.initialValues = {...action.data, OldImage: action.data.Image};
      return stateCopy;
		}
		default:{
			return state;
		}
	}
}

export const actionCreator = {
  setData: (data) => {
    return {type: "SET_DATA_P", data: data};
  },
  setInitialValues: (data) => {
      return {type: "SET_INITIAL_VALUES_P", data: data};
  }
}

export const ThunkCreator = {
  Get: () => {
    return (dispatch) => {
      Api.Get().then((data) => {
        dispatch(actionCreator.setData(data.Data));
      });
    }
  },
  Add: (Data) => {
    let form = new FormData()
    form.append("Image", Data.Image)
    form.append("OldImage", Data.OldImage)
    form.append("Name", Data.Name)
    form.append("Description", Data.Description)
    form.append("IdCategory", Data.IdCategory)
    form.append("Branches", JSON.stringify(Data.Branches))

    return (dispatch) => {
      Api.Insert(form).then((response) => {
        if(!response.Error){
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  },
  Change: (Data) => {
    let form = new FormData()
    form.append("Id", Data.Id)
    form.append("Image", Data.Image)
    form.append("OldImage", Data.OldImage)
    form.append("Name", Data.Name)
    form.append("Description", Data.Description)
    form.append("IdCategory", Data.IdCategory)
    form.append("Branches", JSON.stringify(Data.Branches))

    return (dispatch) => {
      Api.Change(form).then((response) => {
        if(!response.Error){
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  },
  Del: (Data) => {

    return (dispatch) => {
      Api.Delete(Data).then((response) => {
        if(!response.Error){
          Api.Get().then((data) => {
            dispatch(actionCreator.setData(data.Data));
          });
          dispatch(NotificationsAC.SetNotification({Type: "Message", Message: response.Message}));
        }else{
          dispatch(NotificationsAC.SetNotification({Type: "Error", Message: response.Message}));
        }
      });
    }
  }
}

export default Reducer

